import React from 'react';
import './App.css';
import MicRecorder from 'mic-recorder-to-mp3';

const Mp3Recorder = new MicRecorder({ bitRate: 128 });

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			isRecording: false,
			blobURL: '',
			isBlocked: false,
			recordIsVisible: true,
			stopIsVisible: false,
		};
	}

	start = () => {
		if (this.state.isBlocked) {
			console.log('Permission Denied');
			this.askMicrophonePermissions();
		} else {
			Mp3Recorder.start()
				.then(() => {
					this.setState({
						isRecording: true,
						recordIsVisible: false,
						stopIsVisible: true,
					});
				})
				.catch((e) => console.error(e));
		}
	};

	stop = () => {
		Mp3Recorder.stop()
			.getMp3()
			.then(([buffer, blob]) => {
				const blobURL = URL.createObjectURL(blob);
				this.setState({
					blobURL,
					isRecording: false,
					recordIsVisible: true,
					stopIsVisible: false,
				});
			})
			.catch((e) => console.log(e));
	};

	// componentDidMount() {
	// 	navigator.getUserMedia(
	// 		{ audio: true },
	// 		() => {
	// 			console.log('Permission Granted');
	// 			this.setState({ isBlocked: false });
	// 		},
	// 		() => {
	// 			console.log('Permission Denied');
	// 			this.setState({ isBlocked: true });
	// 		}
	// 	);
	// }

	askMicrophonePermissions() {
		navigator.getUserMedia(
			{ audio: true },
			() => {
				console.log('Permission Granted');
				this.setState({ isBlocked: false });
			},
			() => {
				console.log('Permission Denied');
				this.setState({ isBlocked: true });
			}
		);
	}

	render() {
		return (
			<div className='App'>
				<header className='App-header'>
					{this.state.recordIsVisible ? (
						<div
							onClick={this.start}
							disabled={this.state.isRecording}
							className='startButton'
						></div>
					) : null}
					{this.state.stopIsVisible ? (
						<button
							onClick={this.stop}
							disabled={!this.state.isRecording}
						>
							Stop
						</button>
					) : null}
					<audio
						className='audioControls'
						src={this.state.blobURL}
						controls='controls'
					/>
				</header>
			</div>
		);
	}
}

export default App;
